              icon: mdiFormatListBulleted,
              route: "PlanoAlimentarCalcularNovo",
            },
            {
              title: "Tabela de Substituição",
              icon: mdiCached,
              route: "PlanoAlimentarCalcularNovo",
            },
          ],
        },
        {
          title: "Prescrever",
          icon: mdiDraw,
          subitems: [
            {
              title: "Criar Fórmula",
