          icon: mdiChartBarStacked,
          subitems: [
            {
              title: "Atendimento",
              icon: mdiAccountSupervisor,
              route: "PlanoAlimentarCalcularNovo",
            },
            {
              title: "Convênio",
              icon: mdiHospitalBuilding,
              route: "PlanoAlimentarCalcularNovo",
            },
            {
              title: "Idade",
              icon: mdiHumanCane,
              route: "PlanoAlimentarCalcularNovo",
            },
            {
              title: "Sexo",
              icon: mdiGenderMaleFemale,
              route: "PlanoAlimentarCalcularNovo",
            },
            {
              title: "Prescrições",
              icon: mdiDraw,
              route: "PlanoAlimentarCalcularNovo",
            },
            {
              title: "Exames Laboratoriais",
              icon: mdiNeedle,
              route: "PlanoAlimentarCalcularNovo",
            },
            {
              title: "Estatísticas de Consultas",
              icon: mdiChartLine,
              route: "PlanoAlimentarCalcularNovo",
            },
          ],
        },
        { title: "Meu Financeiro", icon: mdiCurrencyUsd },
        {
          title: "Ajuda",
          icon: mdiHelpCircleOutline,
          subitems: [
            {
              title: "Manual",
              icon: mdiBookOpenVariant,
              route: "PlanoAlimentarCalcularNovo",
            },
            {
              title: "Suporte e Dúvidas",
              icon: mdiHelpCircleOutline,
              route: "PlanoAlimentarCalcularNovo",
            },
          ],
        },
      ],
      icons: { mdiAccountDetailsOutline, mdiClose },
      color: "primary",
      colors: ["primary", "blue", "success", "red", "teal"],
      right: false,
      permanent: true,
      miniVariant: false,
      expandOnHover: false,
      background: false,
    };
  },
  firestore () {
    return {
      userInfos: firebase
        .firestore()
        .collection("users")
        .doc(sessionStorage.getItem('current-user')),
        // .doc(firebase.auth().currentUser.email),
    };
  },
  // mounted () {
  //   if (this.$store.state.firestore.alimentos.length === 0) {
  //     this.$store.dispatch('firestore/getAlimentos')
  //   }
  // }
